export const ItemRowStyles = {
    itemRowContainer: {
        marginLeft: '1em',
        display: 'flex',
        flexDirection: 'column',
        marginBottom: '.75em',
        maxWidth: '100vw',
        cursor: 'pointer',
    },

    itemHeader: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'baseline',
    },

    checkbox: {
        cursor: 'pointer',
        fontSize: '2.15em',
    },

    itemName: {
        margin: '0 .25em',
        color: '#DB6865',
        fontFamily: 'Josefin Sans',
    },

    itemQuantity: {
        margin: '0 0 0 .5em',
    },

    itemContent: {
        marginLeft: '.5em',
        display: 'flex',
        flexDirection: 'row',
    },

    hideItemDetails: {
        display: 'none',
    },

    itemDetails: {
        alignSelf: 'top',
        borderRight: '.05em solid black',
        paddingRight: '.5em',
    },

    itemDetailsHidden: {
        marginLeft: 0,
        alignSelf: 'baseline',
    },

    userDetails: {
        alignSelf: 'baseline',
        marginLeft: '.5em'
    },

    date: {
        fontSize: '.75em'
    },

    itemIcons: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        cursor: 'pointer'
    },

    desktopButtons: {
        display: 'none',     
    },

    '@media (min-width: 800px)': {
        desktopButtons: {
            display: 'inline-block',
        }
    },
 
};


